import DdzBaseView from "./DdzBaseView";
import DdzGameEvent from "../data/DdzGameEvent";
import { DdzAudioConst } from "../data/DdzPathHelper";
import DdzDriver from "../DdzDriver";
import DdzGameConst from "../data/DdzGameConst";

/**
 * 出牌等按钮的view
 */
export default class DdzAskActionView extends DdzBaseView {
    private autoBtn: cc.Node;
    private cancelAuto: cc.Node;
    private curView: any;
    public multActionView: DdzMultActionView;
    public robActionView: DdzRobDzActionView;
    public playActionView: DdzPlayActionView;
    public cannotPlayActionView: DdzCannotPlay;
    public rewardActionView: DdzRewardActionView;

    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.autoBtn = this.getChild('autoBtn');
        this.autoBtn.active = false;
        DdzGameConst.addCommonClick(this.autoBtn, "", this.onAutoPlayClick, this);
        this.cancelAuto = this.getChild('cancelAuto');
        this.cancelAuto.active = false;
        DdzGameConst.addCommonClick(this.node, "cancelAuto/btnCancel", this.onCancelAutoClick, this);

        this.robActionView = new DdzRobDzActionView(this.getChild('rob'));
        this.robActionView.active = false;
        this.multActionView = new DdzMultActionView(this.getChild('mult'));
        this.multActionView.active = false;
        this.playActionView = new DdzPlayActionView(this.getChild('play'));
        this.playActionView.active = false;
        this.cannotPlayActionView = new DdzCannotPlay(this.getChild('notPlay'));
        this.cannotPlayActionView.active = false;
        this.rewardActionView = new DdzRewardActionView(this.getChild('reward'));
        this.rewardActionView.active = false;
    }

    private onAutoPlayClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        this.setAutoPlayBtnShow(false);
        Game.Server.send(this.Define.CmdAuto, { auto_play: 1 });
    }

    private onCancelAutoClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        this.setAutoPlayBtnShow(true);
        Game.Server.send(this.Define.CmdAuto, { auto_play: 0 });
    }

    public setAutoPlayBtnShow(isShow: boolean) {
        this.autoBtn.active = isShow;
        this.cancelAuto.active = !isShow;
    }

    public setRobViewShow(isShow: boolean, land_items = [], miss_items = []) {
        this.robActionView.active = isShow;
        if (isShow) {
            this.robActionView.disableMissItem(miss_items);
            this.curView = this.robActionView;
        }
        else
            this.curView = null;
    }

    public setMultViewShow(isShow: boolean) {
        this.multActionView.active = isShow;
        if (isShow)
            this.curView = this.multActionView;
        else
            this.curView = null;
    }

    public setPlayViewShow(isShow: boolean, isPrompt = true, isPass = true) {
        this.playActionView.active = isShow;
        if (isShow) {
            this.playActionView.enablePromptBtn(isPrompt);
            this.playActionView.enablePassBtn(isPass);
            this.curView = this.playActionView;
        }
        else
            this.curView = null;
    }

    public setCannotPlayViewShow(isShow: boolean) {
        this.cannotPlayActionView.active = isShow;
        if (isShow)
            this.curView = this.cannotPlayActionView;
        else
            this.curView = null;
    }

    public getActionClockWorldPos() {
        if (this.curView)
            return this.curView.getClockWorldPos();
        else
            return this.node.convertToWorldSpaceAR(cc.v2(0, 50));
    }

    public showRewardActionView(isShow: boolean) {
        this.rewardActionView.active = isShow;
    }

    protected onClose() {
        this.curView = null;
    }

    public clearByRound() {
        this.autoBtn.active = false;
        this.cancelAuto.active = false;
    }

    public clearByGame() {
        this.autoBtn.active = false;
        this.cancelAuto.active = false;
        this.robActionView.active = false;
        this.multActionView.active = false;
        this.playActionView.active = false;
        this.cannotPlayActionView.active = false;
        this.rewardActionView.active = false;
        this.curView = null;
    }
}

class DdzRobDzActionView extends DdzBaseView {
    private clockRoot: cc.Node;
    private itemList: RobItem[] = [];
    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.clockRoot = this.getChild('clockRoot');
        for (let i = 0; i <= 3; i++) {
            let btnNode = this.getChild('btn' + i);
            this.itemList.push(new RobItem(btnNode, i, this.onRobAction, this));
        }
    }

    public disableMissItem(items: number[] = []) {
        this.itemList.forEach(item => {
            let num = item.robData;
            if (items.indexOf(num) > -1)
                item.setDisable(true);
            else
                item.setDisable(false);
        })
    }

    private onRobAction(num) {
        this.active = false;
        Game.Server.send(this.Define.CmdLandlord, { selected: num });
    }

    public getClockWorldPos() {
        let pos = this.clockRoot.parent.convertToWorldSpaceAR(this.clockRoot.position);
        return pos;
    }
}

class RobItem extends DdzBaseView {
    private btn: cc.Button;
    constructor(node, private data: number, private callback: Function, private target: any) {
        super();
        this.setNode(node);
    }

    protected initView() {
        DdzGameConst.addCommonClick(this.node, "", this.onRobBtnClick, this);
        this.btn = <cc.Button>this.getComponent('', cc.Button);
    }

    private onRobBtnClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        if (this.callback && this.target)
            this.callback.call(this.target, this.data);
    }

    public setDisable(flag) {
        Global.UIHelper.setNodeGray(this.node, flag);
        this.btn.interactable = !flag;
    }

    public get robData() {
        return this.data;
    }
}

class DdzMultActionView extends DdzBaseView {
    private clockRoot: cc.Node;
    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        DdzGameConst.addCommonClick(this.node, "btn0", this.onNoneMultClick, this);
        DdzGameConst.addCommonClick(this.node, "btn1", this.onMultClick, this);
        this.clockRoot = this.getChild('clockRoot');
    }

    private onNoneMultClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        this.active = false;
        Game.Server.send(this.Define.CmdMult, { selected: 1 });
    }

    private onMultClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        this.active = false;
        Game.Server.send(this.Define.CmdMult, { selected: 2 });
    }

    public getClockWorldPos() {
        let pos = this.clockRoot.parent.convertToWorldSpaceAR(this.clockRoot.position);
        return pos;
    }
}

class DdzPlayActionView extends DdzBaseView {
    private clockRoot: cc.Node;
    private playBtn: cc.Button;
    private _canPlay: boolean;
    private promptBtn: cc.Button;
    private passBtn: cc.Button;
    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        DdzGameConst.addCommonClick(this.node, "btn0", this.onNoPlayClick, this);
        this.passBtn = <cc.Button>this.getComponent('btn0', cc.Button);
        DdzGameConst.addCommonClick(this.node, "btn1", this.onPromptClick, this);
        this.promptBtn = <cc.Button>this.getComponent('btn1', cc.Button);
        DdzGameConst.addCommonClick(this.node, "btn2", this.onPlayClick, this);
        this.playBtn = <cc.Button>this.getComponent('btn2', cc.Button);
        this.clockRoot = this.getChild('clockRoot');
    }

    private onNoPlayClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        this.active = false;
        Game.Server.send(this.Define.CmdPass, {});
    }

    private onPromptClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        let result = this.PlayRuleHelper.getOneRecommend();
        if (result) {
            Game.Event.event(DdzGameEvent.OnSelfPromptPokers, result);
        }
        else {
            this.PlayRuleHelper.resetRecommendGen();
            let result = this.PlayRuleHelper.getOneRecommend();
            if (result) {
                Game.Event.event(DdzGameEvent.OnSelfPromptPokers, result);
            }
        }
    }

    public enablePromptBtn(flag: boolean) {
        Global.UIHelper.setNodeGray(this.promptBtn.node, !flag);
        this.promptBtn.interactable = flag;
    }

    public enablePassBtn(flag: boolean) {
        Global.UIHelper.setNodeGray(this.passBtn.node, !flag);
        this.passBtn.interactable = flag;
    }

    private onPlayClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        let curSelectPokers = this.Context.getSelectPokers();
        let curSelectPlayData = this.PlayRuleHelper.getPokersType(curSelectPokers);
        let type = curSelectPlayData.type;
        Game.Server.send(this.Define.CmdPlay, { type: type, cards: curSelectPokers });
        this.Context.set(this.Define.FieldOnPlayAction, true);
        this.active = false;
    }

    protected onOpen() {
        this.enablePlayBtn();
        Game.Event.on(DdzGameEvent.EnableSelect, this, this.enablePlayBtn);
    }

    public set CanPlay(flag: boolean) {
        if (this._canPlay === flag)
            return;
        this._canPlay = flag;
        Global.UIHelper.setNodeGray(this.playBtn.node, !flag);
        this.playBtn.interactable = flag;
    }

    private enablePlayBtn() {
        let isCanPlay = false;
        let curSelectPokers = this.Context.getSelectPokers();
        if (curSelectPokers.length > 0) {
            let curOnPlayData = this.Context.get(this.Define.FieldOnOutPokers);
            let curSelectPlayData = this.PlayRuleHelper.getPokersType(curSelectPokers);
            isCanPlay = this.PlayRuleHelper.checkPlayOnRule(curOnPlayData, curSelectPlayData);
        }
        this.CanPlay = isCanPlay;
    }

    public getClockWorldPos() {
        let pos = this.clockRoot.parent.convertToWorldSpaceAR(this.clockRoot.position);
        return pos;
    }

    protected onClose() {
        Game.Event.off(DdzGameEvent.EnableSelect, this, this.enablePlayBtn);
    }
}

