class DdzRewardActionView extends DdzBaseView {
    private clickLimit = false;
    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        DdzGameConst.addCommonClick(this.node, "gameBtn", this.onGameClick, this);
        DdzGameConst.addCommonClick(this.node, "exitBtn", this.onExitClick, this);
    }

    private onGameClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        if (this.clickLimit)
            return;
        DdzDriver.instance.reMatchPlayer();
        this.clickLimit = true;
        Game.Component.scheduleOnce(() => {
            this.clickLimit = false;
        }, 3);
    }

    private onExitClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        Game.Server.send(this.Define.CmdLeave, { "IsClose": 1 });
        DdzDriver.instance.leaveGame();
    }

    protected onOpen() {
        this.clickLimit = false;
    }
}